<template>
      <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
             
          </a>
        </li>
      </ul>

      <h1 class="content__title" v-if="orderInfo">
          <span>{{ orderInfo.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
             &nbsp;  . &nbsp;    &nbsp; .
               &nbsp; &nbsp;     .
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value" v-if="orderInfo">
                {{ orderInfo.name }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value" v-if="orderInfo">
                 {{ orderInfo.address }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                
              </span>
              <span class="dictionary__value" v-if="orderInfo">
                {{ orderInfo.phone }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value" v-if="orderInfo">
                {{ orderInfo.email }}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                 
              </span>
              <span class="dictionary__value">
                  
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem  v-for="item in orderProducts" :key="item.productId" :item="item" />
          </ul>

          <div class="cart__total" v-if="orderInfo">
            <p>: <b> </b></p>
            <p> : <b>{{ orderInfo.totalAmount }}</b>   <b>{{ orderInfo.totalPrice | numberFormat }} </b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import OrderItem from '@/components/OrderItem.vue';
import { mapGetters } from 'vuex';

export default {
  filters: { numberFormat },
  components: { OrderItem },
  computed: {
    ...mapGetters({ orderProducts: 'orderProducts' }),
    orderInfo() {
      return this.$store.state.orderInfo;
    },
  },
  created() {
    if (
      this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id
    ) {
      return;
    }

    this.$store.dispatch('loadOrderInfo', this.$route.params.id);
  },
};

</script>
